body {
  margin: 0;
  padding: 0;
  background: #eee;
  color: #333;
}

.jumbotron{
  background: #fff;
}
#container-header h1{
  text-decoration: none;
  color:#333;
}
#container-header a:hover{
  color: #333;
  text-decoration: none;
  cursor: pointer;
} 


#container-tareas{
  background: #fff;
}
#container-tarea{
  border-bottom: solid #bbb 1px;
}
#tarea-nombre{
  color:#000;
}
#container-tarea button{
  padding: 0px 8px;
  margin-right: 10px;
}